import React, { useState, useEffect } from 'react';

import Button from '../blocks/Button/Button.js';
import Navigation from '../blocks/Navigation/Navigation.js';
import CommentsBlock from './CommentsBlock/CommentsBlock.js';
import Date from '../blocks/Date/Date.js';
import Author from '../blocks/Author/Author.js';
import Paragraph from '../blocks/Paragraph/Paragraph.js';
import InstagramPost from '../blocks/InstagramPost/InstagramPost.js';
import Comercial from '../blocks/Comercial/Comercial.js';

import mainPhoto from '../../assets/photo/main-photo.png';
import quotation from '../../assets/svg/quatation-mark.svg';

import banner3 from '../../assets/photo/banner3.png';
import banner4 from '../../assets/photo/banner4.png';

import data from '../../data/Data.js';
import Promotion from '../blocks/Promotion/Promotion.js';


//dodac text1, text2  w instagramPost

const Blog = () => {
  const [isTablet, setTablet] = useState(window.innerWidth > 625);

  const updateMedia = () => {
    setTablet(window.innerWidth > 625);
  };

  useEffect(() => {
    window.addEventListener('resize', updateMedia);
    return () => window.removeEventListener('resize', updateMedia);
  });

  return (
    <section className='blog'>
      <div className='blog__links'>
        <Navigation text={'Aktualnoci'} styleName={'blog__links__item'} /> /{' '}
        <Navigation text={'Przemiany gwiazd'} styleName={'blog__links__item'} />{' '}
        / <span>"Wow! Niesamowity sekret Kylie..."</span>
      </div>
      <Button styleName='blog_btn' btnLink='/' text='Przemiany gwiazd' />
      <div className='blog__info'>
        <Date />
        <Author />
      </div>
      <h1 className='blog__title'>
        Wow! Niesamowity sekret Kylie Jenner odkryty  dziki{' '}
        <span className='paragraph__text-pink'>TEJ</span> prostej metodzie z
        atwoci powikszysz i ujdrnisz biust oraz poladki tak jak ona!
      </h1>
      <img className='blog__img' src={mainPhoto} alt='kylie-jenner'></img>
      <Paragraph>
        Syszelicie ju najnowsze ploteczki? Jedna z najbardziej znanych
        dwudziestolatek, gwiazda urodzona w najseksowniejszej rodzinie na
        wiecie  Jenner-Kardashian  wyznaa ostatnio co zaskakujcego.
        Okazuje si, e jej obfity, ksztatny biust i poladki nie s wynikiem
        genetyki, ani  co dziwniejsze  chirurgii plastycznej. Za te pikne,
        kobiece ksztaty odpowiada{' '}
        <span className='paragraph__text-pink-bold'>
          nieinwazyjna naturalna metoda!
        </span>
        Gdyby celebrytka nie przyznaa tego sama, ciko byoby uwierzy,{' '}
        <span className='paragraph__text-pink-bold'>
          e swoje nieziemskie krgoci zawdzicza wycznie temu innowacyjnemu
          elowi.
        </span>
      </Paragraph>
      <Paragraph>
        Ta wiadomo wstrzsna fanami. Niech kada z nas zada sobie pytanie 
        <span className='paragraph__text-bold'>
          czy gdyby istniaa moliwo poprawienia swoich walorw, bez wydawania
          duej iloci pienidzy, nie chciaybymy z niej skorzysta?
        </span>{' '}
        W kocu speni marzenie o idealnych krgociach bez koniecznoci
        wydawania ogromnych iloci pienidzy czy kadzenia si pod n
        operacyjny? Czy gdyby metody znane Kylie byy dostpne na wycignicie
        rki  za pienidze nieporwnywalnie niskie w stosunku do operacji
        plastycznej  nie chciaybymy sprbowa?
      </Paragraph>
      <Paragraph>
        Nasza redakcja na pewno powiedziaaby:{' '}
        <span className='paragraph__text-bold'>TAK!</span> A Wy jak, Kochane? ;)
      </Paragraph>
      <h2 className='blog__subtitle'>
        Sprawdmy, gdzie tkwi sekret idealnych piersi
      </h2>
      <Paragraph>
        Kylie niedawno urodzia swoje pierwsze dziecko. Kiedy w formie lepszej
        ni sprzed ciy pojawia si na okadkach najbardziej znanych
        tabloidw, fani byli w szoku.{' '}
        <span className='paragraph__text-pink-bold'>
          Figura gwiazdy wygldaa idealnie  paski brzuch, krge, jdrne
          poladki i ten doskonay, WIELKI BIUST!
        </span>
      </Paragraph>
      <Paragraph>
        <span className='paragraph__text-bold'>Jak ona to zrobia?</span>
      </Paragraph>
      <Paragraph>
        Gwiazda zdradzia swj sekret na Instagramie. Kylie nie wstydzi si
        mwi o metodzie, ktra sprawia, e jej piersi wygldaj idealnie!
        Jeszcze niedawno wspominaa te o wypeniaczach, dziki ktrym jej usta
        wygldaj tak pontnie i seksownie. Dzisiaj zdradza nam sekret swoich
        pontnych krgoci.
      </Paragraph>
      <div className='quotation'>
        <img className='quotation__img' src={quotation} alt='' />
        <Paragraph>
          <span className='paragraph__text-italic'>
            Sekrety mojej sylwetki nie s adn tajemnic! Dementuj wszystkie
            plotki dotyczce tego, e miaam jakiekolwiek operacje plastyczne.
            Wszystko, co osignam w pracy nad sylwetk, udao mi si dokona
            cakowicie naturalnymi metodami. W przypadku biustu i poladkw to
            wanie ten el w 1 miesic doda im objtoci. Sensual Shape to
            produkt w peni naturalny [sprawdzalimy skad  zero chemii,
            wycznie naturalne skadniki  red.], ktry poprzez stymulacj
            tkanki tuszczowej powiksza piersi i poladki!
          </span>
        </Paragraph>
        <img className='quotation__img' src={quotation} alt='' />
      </div>
      <div className='instagram-posts-wrapper'>
        <InstagramPost
          avatar={data.instagramPost1.avatar}
          name={data.instagramPost1.name}
          verified={data.instagramPost1.verified}
          followers={data.instagramPost1.followers}
          mainPhoto={data.instagramPost1.mainPhoto}
          numberOfLikes={data.instagramPost1.numberOfLikes}
          text={data.instagramPost1.text}
          postHashtags={data.instagramPost1.postHashtags}
          numberOfComments={data.instagramPost1.numberOfComments}
        />
        <Paragraph>
          Niestety, ze wzgldu na burz, jak wywoaa ta wiadomo wrd
          chirurgw plastycznych, post gwiazdy ZOSTA USUNITY!!! Jak podaj
          zagraniczne media,{' '}
          <span className='paragraph__text-bold'>
            konto Kylie zostao zhakowane
          </span>{' '}
          w wyniku spisku klinik medycyny estetycznej, ktre za wszelk cen
          chc ukry informacj o nowym naturalnym elu powikszajcym piersi.
          Na szczcie nasze fantastyczne{' '}
          <span className='paragraph__text-bold'>
            koleanki z redakcji zdyy wczeniej pobra post,
          </span>{' '}
          ktry wrzucilimy specjalnie dla Was!
        </Paragraph>
        <InstagramPost
          avatar={data.instagramPost2.avatar}
          name={data.instagramPost2.name}
          verified={data.instagramPost2.verified}
          followers={data.instagramPost2.followers}
          mainPhoto={data.instagramPost2.mainPhoto}
          numberOfLikes={data.instagramPost2.numberOfLikes}
          text={data.instagramPost2.text}
          postHashtags={data.instagramPost2.postHashtags}
          numberOfComments={data.instagramPost2.numberOfComments}
        />
      </div>
      <Paragraph>
        Ta formua to prawdziwy hit na rynku! Zawiera fitoestrogeny, ktre w
        zupenie naturalny sposb{' '}
        <span className='paragraph__text-pink-bold'>
          powikszaj biust o 3 rozmiary w zaledwie miesic!
        </span>{' '}
        Cay wiat dosownie oszala na jego punkcie. Jak si okazao, produkt
        od niedawna dostpny jest te w Polsce, a Instagram a kipi od Waszych
        cudownych zdj przed i po! &hearts;
      </Paragraph>
      <div className='instagram-posts-wrapper'>
        {data.instagramSection.map((instagramPost, index) => {
          return (
            <InstagramPost
              key={index + 'yu545nvbskj664bvs'}
              avatar={instagramPost.avatar}
              name={instagramPost.name}
              city={instagramPost.city}
              mainPhoto={instagramPost.mainPhoto}
              numberOfLikes={instagramPost.numberOfLikes}
              text={instagramPost.text}
              postHashtags={instagramPost.postHashtags}
              numberOfComments={instagramPost.numberOfComments}
            />
          );
        })}
      </div>
      <Paragraph>
        Nasza redakcja zawsze wnikliwie sprawdza skad, gdy syszy o konkretnych
        nowociach  dlatego chcielimy si dowiedzie, co takiego niesamowitego
        kryje si w Sensual Shape, ktry sta si HITEM. Okazuje si, e to
        cakowicie naturalny rodek, ktry stymuluje tkank tuszczow i
        ujdrnia miejsca w obszarach, na ktre pozostaje zaaplikowany. Dziki
        regularnemu stosowaniu i aplikacji,{' '}
        <span className='paragraph__text-bold'>
          piersi wzrastaj nawet o 3 rozmiary w zaledwie 1 miesic, a pupa
          powiksza si i zaokrgla identycznie, jak u wszystkich Kardashianek!
        </span>
      </Paragraph>
      <Paragraph>
        Ploteczki donosz, e uywaj jej te inne gwiazdy, ktre mog pochwali
        si wietnymi krgociami, takie jak Jennifer Lopez, Jennifer Aniston i
        Mila Kunis.
      </Paragraph>
      <Paragraph>
        My te zamwiymy ten el i na pewno podzielimy si z Wami efektami w
        kolejnych artykuach. Wiemy ju, gdzie mona go kupi  na tej stronie
        moecie otrzyma go ze specjaln znik!
      </Paragraph>
      {
        <>
          {isTablet ? (
            <Comercial
              blogWide={true}
              bgImage={banner4}
              btnTilte={'Kliknij i sprawd'}
            />
          ) : (
            <Comercial
              blogSmall={true}
              bgImage={banner3}
              btnTilte={'Sprawd!'}
            />
          )}
        </>
      }
      <div className='promotion-wrapper'>
        <Promotion>
          <span className='promotion__text-italic'>
            Kliknij w baner powyej i zamw el sensual shape ze znik 70%!
          </span>
        </Promotion>
        <Paragraph>
          Ilo produktw objtych promocj jest ograniczona. Zamw Sensual
          Shape i uzyskaj idealne ciao zaledwie w jeden miesic!
        </Paragraph>
        <Promotion>
          <span className='promotion__text-underline'>
            Tylko na tej stronie moesz zamwi oryginalny certyfikowany produkt
            ze specjaln znik!
          </span>
        </Promotion>
        <Paragraph>
          <span className='paragraph__text-bold'>UWAGA!</span> Naszej redakcji
          udao si dotrze do producenta oryginalnego elu i otrzyma specjaln
          znik <span className='paragraph__text-bold'> 70%</span> dla
          czytelnikw! Jednak musicie si pospieszy  ilo produktw objtych
          promocj jest ograniczona.
          Mamy tylko <span className='paragraph__text-bold'>200 sztuk!</span>
        </Paragraph>
      </div>
      <CommentsBlock />
    </section>
  );
};

export default Blog;
